<template>
	<div class="maple-form">
		<slot></slot>
	</div>
</template>
<script>
	export default {
		provide() {
			return {
				form: this, // 
			}
		},
		props: {
			model: {
				type: Object,
				required: true
			},
			rules: {
				tyoe: Object
			}
		},
		data() {
			return {
				files: []
			}
		},
		created() {
			// 
			this.$on('formItemAdd', item => this.files.push(item));
			console.log(this.files);
		},
		methods: {
			// FormItem validate() promise
			//  LForm  validate 
			async validate(callback) {
				let tasks = this.files.map(item => item.validate())
				console.log(tasks)

				// 
				const results = await Promise.all(tasks)

				let ret = true
				results.forEach(valid => {
					if (!valid) {
						ret = false // 
					}
				})
				callback(ret)
			}
		},
	}
</script>
