<template>
	<div class="maple-form-item">
		<label :for="prop">{{label}}</label>
		<slot></slot>
		<span :class="['tips', {hidden: validateStatus !== 'error'}]">{{validateMsg}}</span>
	</div>
</template>
<script>
	import schema from 'async-validator';

	export default {
		inject: ["form"], // form   form  model  rules
		props: {
			prop: {
				type: String,
				default: ''
			},
			label: {
				type: String,
				default: ''
			}
		},
		data() {
			return {
				validateStatus: '',
				validateMsg: ''
			}
		},
		created() {
			//   
			this.$on('validate', this.validate);
		},
		mounted() {
			// form
			//  props  
			if(this.prop) {
				this.$parent.$emit("formItemAdd", this)
			}
		},
		methods: {
			validate() {
				//  async-validator 
				return new Promise((resolve) => {
					console.log('validate -- input ', this.form.model[this.prop])
					// 
					let descriptor = {
						[this.prop]: this.form.rules[this.prop]
					}

					// 
					let validator = new schema(descriptor)

					// 
					validator.validate({[this.prop]: this.form.model[this.prop]}, (error) => {
						if(error) {
							this.validateStatus = 'error'
							this.validateMsg = error[0].message

							resolve(false)
						} else {
							this.validateStatus = ''
							this.validateMsg = ''

							resolve(true)
						}
					})
				})
			}
		},
	}
</script>
<style lang="less" scoped>
	@import "../../style/color";
	@import "../../style/size";
	.maple-form-item {
		label {
			font-size: @formitem-label;
			display: block;
			padding-bottom: 4px;
		}
		.tips {
			font-size: @fontSize-small;
			color: @color-danger;
			height: 26px;
			display: block;
			line-height: 24px;
		}
	}
</style>
